/* ProductListingPage.module.css */

.gridContainer {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    margin: 20px;
}

.gridItem {
    border: 1px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
    cursor: pointer;
}

.gridItem img {
    width: 100%;
    max-height: 200px;
    object-fit: cover;
}

.gridItem p {
    text-align: center;
    margin: 10px 0;
}

.noResults {
    text-align: center;
    margin-top: 20px;
    font-size: 18px;
    color: #777;
}

.gridItem {
    position: relative;
}

.toggleFilter {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px;
    display: flex;
    justify-content: space-between;
    cursor: pointer;
}