/* FilterComponent.module.css */

.filterContainer {
    position: sticky;
    bottom: 20;
    left: 0;
    background-color: #fff;
    gap: 20px;
    margin: 20px;
    border-top: 1px solid #ccc;
    max-height: 40vh;
    /* Adjust the max height as needed */
    overflow-y: auto;
    /* Add overflow-y to enable vertical scrolling */
}

.filterOptions {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 10px;
}

.filterOption {
    margin-bottom: 10px;
}

.checkboxContainer {
    display: grid;
    gap: 5px;
}

.checkboxLabel {
    display: flex;
    align-items: center;
}

.filterButtons {
    display: flex;
    justify-content: space-between;
}
